    constructor(gameContainer: HTMLDivElement, config: {
        debug?: true,
        mobileLog?: true,
        logLevel: number,
        assetsBaseUrl?: string,
        lobbyUrl?: string,
        startingBalance?: number,
        serverApiUrl: string,
        jwtToken: string,
        gameId: string,
        language: string,
        historyApiUrl: string,
        operatorId?: string,
        currency?: string,
        userLoginUrl?: string,
    }) {
        if (config.mobileLog && utils.isMobile.any) {
            new MobileBrowserLog();
        }

        if (utils.isMobile.any) {
            SoundManager.mute = false;

            document.addEventListener('visibilitychange', () => {
                if (document.visibilityState === 'visible') {
                    // Ticker.shared.stop();
                    SoundManager.mute = false;
                } else {
                    // Ticker.shared.start();
                    SoundManager.mute = true;
                }
            });
        }

        Logger.info(`EX - princess of atlantis [${VERSION}]`);

        Logger.logLevel = config.logLevel;

        super(gameContainer, {
            id: 'game',
            container: gameContainer,
            resizeMethod: ResizeMethod.SHOW_ALL,
            backgroundColor: 0x000000,
            baseWidth: 1920,
            baseHeight: 1080,
            autoUpdateSizeToOrientation: true,
            debug: config.debug,
            fps: ((getFromLocalStorage('settings')?.batterySaver ?? false) == true) ? 20 : 60
        });
        this.language = config.language;
        if (config.debug) {
            globalThis.__PIXI_STAGE__ = this.pixi.stage;
            globalThis.__PIXI_RENDERER__ = this.pixi.renderer;
        }
        Logger.warning(`FPS VALUE - ${((getFromLocalStorage('settings')?.batterySaver ?? false) == true) ? 20 : 60}`);

        this.gameService = new GameService(
            config.serverApiUrl,
            config.historyApiUrl,
            config.jwtToken,
            config.gameId,
            config.lobbyUrl,
            config.operatorId,
            config.currency,
            config.userLoginUrl
        );
        this.container.style.display = 'none';
        container.registerInstance(GameService, this.gameService);

        this.frontController = new SlotGameFrontController();

        container.registerSingleton(UIState);
        container.registerSingleton(PopupState);
        container.registerSingleton(History);

        this.popupManager = new PopupManager();
        this.popupManager.position.set(this.baseWidth / 2, this.baseHeight / 2);
        this.stage.addChild(this.popupManager);

        if (config.assetsBaseUrl)
            this.assetsBaseUrl = config.assetsBaseUrl + this.assetsBaseUrl;


        const loadingScreenAssetsLoader: AssetsConfigLoader = new AssetsConfigLoader(`./assets/loading-screen-assets-config.json`, this.pixi.renderer);
        loadingScreenAssetsLoader.on(AssetsConfigLoader.EVENT_LOADING_COMPLETE, this.onLoadingScreenAssetsLoaded, this);

        const isSound = getFromLocalStorage('settings')?.sound ?? true;
        SoundManager.getChannel('default').mute = !isSound;
        SoundManager.getChannel('ambient').mute = !isSound;

        this.getTranslationFile(jsonAdress[jsonAdress.hasOwnProperty(config.language) ? config.language : 'EN']).then(() => {
            loadingScreenAssetsLoader.load();
        });
    }

    private async getTranslationFile(path: string): Promise<void> {
        // fetch json file
        const response: Response = await fetch(`./assets/translations/${path}.json`);
        const data = await response.json();
        Translation.t = ((key) => {
            return this.getTranslationByKey(key, data);
        });
    }

    private getTranslationByKey(key: string, data): string {
        const keys = key.split('.');

        // Traverse the translation object to find the value
        let nestedTranslation: string | undefined | any = data;
        for (const k of keys) {
            if (!nestedTranslation || typeof nestedTranslation !== 'object') {
                console.error(`Translation key '${key}' not found.`);
                return undefined;
            }
            nestedTranslation = nestedTranslation[k];
        }

        // Return the translation value if found
        return typeof nestedTranslation === 'string' ? nestedTranslation : undefined;
    }

