
import { Spine, TrackEntry } from "@pixi-spine/runtime-4.1";
import AssetsManager from "../../gamma-engine/core/assets/AssetsManager";
import { BrowserApplication } from "../../gamma-engine/core/view/BrowserApplication";
import { Container } from "pixi.js";

export class GameTransitionAnimation extends Container {
    private animation: Spine;
    private lastAlpha: number = 0;
    private transitionTimeScale: number = 0.8;
    private lastAnimation: string = null;
    private resolveCallback: () => void;

    constructor() {
        const width = window.innerWidth;
        const height = window.innerHeight;
        const aspectRatio = +(width / height).toFixed(2);
        const spineAnimation = AssetsManager.spine.get(aspectRatio < 1 ? 'transition-animation-mobile' : 'transition-animation');
        
        super();
        this.animation = new Spine(spineAnimation);
        this.addChild(this.animation);
        this.on('added', this.onAdded, this);
        this.on('removed', this.onRemoved, this);
        window.addEventListener('resize', this.onResize.bind(this));
        this.alpha = 0;
    }

    private onResize(): void {
    const shouldUseMobile = this.isMobile();
    const desiredKey = shouldUseMobile ? 'transition-animation-mobile' : 'transition-animation';
        if (!this.animation || this.animation.spineData !== AssetsManager.spine.get(desiredKey)) {
        if (this.animation) {
            this.removeChild(this.animation);
            this.animation.destroy({ children: true });
        }

        this.animation = new Spine(AssetsManager.spine.get(desiredKey));
        this.addChildAt(this.animation, 0); // lo mandamos al fondo

        if (this.lastAnimation && this.resolveCallback) {
            const entry = this.animation.state.setAnimation(0, this.lastAnimation, false);
            entry.timeScale = this.transitionTimeScale;

            this.animation.state.addListener({
                complete: (entry: TrackEntry) => {
                    if (entry.animation.name === this.lastAnimation) {
                        this.resolveCallback();
                        this.alpha = 0;
                    }
                }
            });
        }
    }
        this.scale.set(this.getScaleByAspectRatio());
    }

    /**
     * Determines the aspect ratio type based on screen dimensions
     */
    private getAspectRatio(): number {
        const width = window.innerWidth;
        const height = window.innerHeight;
        const aspectRatio = +(width / height).toFixed(2);
        return aspectRatio;
    }
    private isMobile(): boolean {
        return this.getAspectRatio() < 1;
    }

    /**
     * Determines the aspect ratio type based on screen dimensions
     */
    private getScaleByAspectRatio(): number {
        const scaleMin = Math.min(BrowserApplication.mainScreenStage.width / 1920, BrowserApplication.mainScreenStage.height / 1080);
        const scaleMax = Math.max(BrowserApplication.mainScreenStage.width / 1920, BrowserApplication.mainScreenStage.height / 1080);
        return this.isMobile() ? scaleMin * 1.8 : scaleMax;
            
    }

    private onAdded(): void {
        this.alpha = this.lastAlpha;
        this.getAspectRatio() < 1 ?  this.position.set(540, 960) : this.position.set(960, 540);
        this.onResize();
    }

    private onRemoved(): void {
        this.lastAlpha = this.alpha;
    }

    public setTransition(transitionName: string): Promise<void> {
        this.lastAnimation = transitionName;
        this.animation.state.setEmptyAnimations(0);
        this['lastTime'] = null;
        return new Promise((resolve) => {
            this.resolveCallback = resolve;
            const entry = this.animation.state.setAnimation(0, transitionName, false);
            entry.timeScale = this.transitionTimeScale;
            this.alpha = 1;
            this.animation.state.addListener({
                complete: (entry: TrackEntry) => {
                    if (entry.animation.name === transitionName) {
                        resolve();
                        this.alpha = 0;
                    }
                }
            });
        })
    }
}
