import { TrackEntry } from '@pixi-spine/runtime-4.1';
import { Spine } from '@pixi-spine/all-4.1';
import { BitmapText, Container, Rectangle, Text } from 'pixi.js';
import { container } from 'tsyringe';
import { Tweener } from '../../../../libs/engineTween';
import AssetsManager from '../../../core/assets/AssetsManager';
import Sound from '../../../core/sound/Sound';
import SoundManager from '../../../core/sound/SoundManager';
import Wallet from '../../../slots/model/Wallet';
import SoundList from '../../sound/SoundList';
import ControlEvent from '../../../core/control/event/ControlEvent';
import { UIPanelEvent } from '../../control/event/UIPanelEvent';

export default class PopupBigWin extends Container {
    private levelAnimationNames: string[] = ['big', 'grand', 'mega', 'super'];

    private levelAnimationDurations: number[] = [3, 3, 3, 3];

    readonly level: number | (() => number);

    private winValue: number | (() => number);
    private _value: number;

    // VISUALS
    public animation: Spine;
    public tfAmount: BitmapText;

    private loopedSound: Sound;
    private isAnimationCompleted: boolean = false;

    constructor(level: number | (() => number), winValue: number | (() => number), scale?: number) {
        super();

        // DisplayShortcuts.init();
        this.winValue = winValue;
        this.once('added', this.onAdded, this);
        this.level = level;

        // const asset = AssetsManager.spine.get('celebrational_wins') 
        //     ?? this.levelAnimationNames.map(name => AssetsManager.spine.get(`${name}_win`));

        this.tfAmount = new BitmapText("", {
            fontName: 'DalekPinpointBold',
            fontSize: 20
        });
        this.tfAmount.anchor.set(0.5, 0.5);
        this.tfAmount.position.y = 50;

        this.once('removed', this.onRemoved, this);

        this.on('pointerdown', this.onPopupClick, this);

        this.interactive = true;
        this.cursor = 'pointer';
        this.eventMode = 'dynamic';
        this.hitArea = new Rectangle(-1000, -1000, 2000, 2000);
    }

    private onPopupClick(): void {
        if (this.isAnimationCompleted) {
            this.interactive = false;
            this.animation.state.setAnimation(0, `${this.levelAnimationNames[this.levelValue]}_win_disappearance`, false);
        } else {
            if (this.loopedSound != null) {
                this.loopedSound.stop();
                this.loopedSound = null;
            }            
            this.isAnimationCompleted = true;
            Tweener.removeTweens(this);
            Tweener.removeTweens(this.tfAmount);
            Tweener.removeTweens(this.tfAmount.scale);
            this.tfAmount.scale.set(3, -3);
            this.tfAmount.alpha = 1;
            this.value = this.winValue instanceof Function ? this.winValue() : this.winValue;
        }
    }

    // PUBLIC API
    public get value(): number {
        return this._value;
    }

    public set value(value: number) {
        this._value = value;

        const wallet: Wallet = container.resolve(Wallet);
        this.tfAmount.text = Wallet.getCurrencyValueFixed(value, 2, true);
    }

    private get levelValue() {
        return this.level instanceof Function ? this.level() : this.level;
    }

    private onAdded(): void {
        if (this.levelValue == -1) {
            throw new Error('Big win level not set');
        }

        const animations = [
            'big_win',
            'grand_win',
            'mega_win',
            'super_win',
        ];

        if (this.animation) {
            this.removeChild(this.animation);
        }

        this.animation = new Spine(AssetsManager.spine.get(animations[this.levelValue]));
        for(let i = 0; i < this.animation.skeleton.slots.length; i++){
                const slot = this.animation.skeleton.slots[i];
                const slotContainer = this.animation.slotContainers[i];
                slotContainer.name = slot.data.name;
            }
        const counterContainer: Container = this.animation.slotContainers[this.animation.skeleton.findSlotIndex('Rectangle_btn')];
        counterContainer.addChild(this.tfAmount);
        counterContainer.name = 'CounterContainer';
        this.animation.scale.set(1.2, 1.2);

        this.animation = this.animation;
        this.addChild(this.animation);

        const levelAnimationName: string = this.levelAnimationNames[this.levelValue];
        const levelAnimationDuration: number = this.levelAnimationDurations[this.levelValue];

        // spec says 5 secs animating, so we put 4 + 1 in + 1 out
        const numLoopAnimations: number = Math.ceil(5 / levelAnimationDuration);
        this.animation.state.setEmptyAnimations(0);
        const countUpDuration: number = 4;
        this.animation.scale.set(0.9, 0.9);
        this.animation.state.addListener({
            start: (entry: TrackEntry) => {
                if (entry.animation.name.includes('_win_appearance')) {
                    this.tfAmount.alpha = 0;
                    Tweener.addTween(this.tfAmount, {
                        alpha: 1,
                        time: 0.3,
                        // angle:90,
                        transition: 'easeOutQuad',
                        delay: 0.3,
                    });

                    this.tfAmount.scale.set(0.2, -0.2);
                    Tweener.addTween(this.tfAmount.scale, {
                        x: 3,
                        y: -3,
                        time: countUpDuration / 8,
                        transition: 'easeOutBack',
                        delay: 0.3,
                    });
                } else if (entry.animation.name.includes('_win_disappearance')) {
                    Tweener.addTween(this.tfAmount, {
                        alpha: 0,
                        time: 0.5,
                        transition: 'easeInQuad',
                        delay: 0.3,
                        onComplete: () => {
                            new ControlEvent(UIPanelEvent.HIDE_POPUP).dispatch();
                        }
                    });
                }
            },
        });

        this.value = 0;
        Tweener.addTween(this, {
            value: this.winValue instanceof Function ? this.winValue() : this.winValue,
            time: countUpDuration,
            transition: 'easeInOutQuad',
            onStart: () => {
                if (this.visible) {
                    this.loopedSound = SoundManager.loop({
                        id: SoundList.COUNTER_LOOP,
                        volume: 0.3,
                    });
                }
            },
            onComplete: () => {
                if (this.loopedSound != null) {
                    this.loopedSound.stop();
                    this.loopedSound = null;
                }
                this.isAnimationCompleted = true;
                // SoundManager.play({
                //     id: SoundList.COUNTER_END,
                //     volume: 0.4,
                // });
            },
        });


        this.animation.stateData.setMix(`${levelAnimationName}_win_appearance`, `${levelAnimationName}_win_loop`, 0.7);
        this.animation.state.setAnimation(0, `${levelAnimationName}_win_appearance`, false);
        this.animation.state.addAnimation(0, `${levelAnimationName}_win_loop`, true, 0);
    }

    private onRemoved(): void {
        if (this.loopedSound) {
            this.loopedSound.stop();
        }
        Tweener.removeTweens(this);
        Tweener.removeTweens(this.tfAmount);
        Tweener.removeTweens(this.tfAmount.scale);
        if (this.animation) {
            this.animation.state.setEmptyAnimations(0);
            this.animation.state.clearListeners();
            this.animation['lastTime'] = null;
        };
        this.removeAllListeners();
    }
}
