    async playIntroSimulation() {
        console.log("Starting Intro Simulation...");
        this.simulationRunning = true;

        while (this.simulationRunning) {
            await this.simulateGameCycle();
        }

        console.log("Intro Simulation Stopped");
    }

    async simulateGameCycle() {
        // Step 1: Detect matches
        const matches = this.detectMatches();

        if (matches.length > 0) {
            // Step 2: Process matches
            await this.processUpdatesSequentially(matches, false);

            // Step 3: Apply gravity
            await this.applyGravity();

            // Step 4: Add new pieces to empty spots
            const newPieces = this.generateNewPieces();
            await this.handleNewPieces(newPieces);

            // Add a small delay before the next cycle
            await this.delay(500);
        } else {
            // No matches detected, end cycle
            console.log("No matches detected. Ending cycle.");
            this.simulationRunning = false; // End simulation if no matches are found
        }
    }


    detectMatches(): any[] {
        const keyCounts: Record<string, number> = {};
        const keyPositions: Record<string, { x: number; y: number }[]> = {};

        // Count occurrences of each key and store their positions
        for (let y = 0; y < this.gridRows; y++) {
            for (let x = 0; x < this.gridCols; x++) {
                const piece = this.grid[y][x];
                if (piece) {
                    const key = piece.key;

                    if (!keyCounts[key]) {
                        keyCounts[key] = 0;
                        keyPositions[key] = [];
                    }

                    keyCounts[key]++;
                    keyPositions[key].push({ x, y });
                }
            }
        }

        // Find the key with the highest count
        let highestKey = null;
        let highestCount = 0;

        for (const key in keyCounts) {
            if (keyCounts[key] > highestCount) {
                highestCount = keyCounts[key];
                highestKey = key;
            }
        }

        if (!highestKey) {
            // No matches found
            return [];
        }

        // Collect all positions for the highest key
        const matches = keyPositions[highestKey].map(pos => ({
            location_x: pos.x,
            location_y: pos.y,
            action: "match",
        }));

        //console.log(`Highest key: ${highestKey} with count: ${highestCount}`);
        //console.log(`Matching positions:`, matches);

        return matches;
    }




    generateNewPieces(): any[] {
        const newPieces = [];
        for (let y = 0; y < this.gridRows; y++) {
            for (let x = 0; x < this.gridCols; x++) {
                if (this.grid[y][x] === null) {
                    newPieces.push({
                        location_x: x,
                        location_y: y,
                        item_id: this.getRandomTileId(),
                    });
                }
            }
        }
        return newPieces;
    }

    stopIntroSimulation() {
        if (this.simulationTimeout) {
            clearTimeout(this.simulationTimeout);
            this.simulationTimeout = null;
        }
        console.log("Stopping Intro Simulation...");
        this.simulationRunning = false;
        this.cleanAll();
    }

    cleanAll(){
        this.simulationRunning = false;
        this.grid = Array.from({ length: this.gridRows }, () => Array(this.gridCols).fill(null));
        this.gridContainer.removeChildren();
        this.dividerContainer.removeChildren();
        this.backgroundContainer.removeChildren();
        this.gridMask.clear();
        this.borderRadiusMask.clear();
        this.destroy();
    }

    getRandomTileId(): number {
        // @ts-ignore
        const tiles = tileMappings.filter(tile => tile.type !== "multiplier");
        if (Math.random() < 0.003) {
            this.numOfMultiplierLanded += 1
            // @ts-ignore

            const multipliers = tileMappings.filter(tile => tile.type === "multiplier");
            if (multipliers.length > 0) {
                return multipliers[Math.floor(Math.random() * multipliers.length)].id;
            }
        }
        //console.log("tiles", tiles)
        return tiles[Math.floor(Math.random() * tiles.length)].id;
    }

