    getSpinDataArray(data: any): any[] {
        // Flatten the spin data based on the 'type'
        let spinDataArray: any[] = [];
        data.forEach((spinTypeData: any) => {
            spinDataArray.push({
                type: spinTypeData.type,
                data: spinTypeData.data
            });
        });
        return spinDataArray;
    }

