        // Update the currentTotalReward
        this.currentTotalReward = newTotalReward;

        // Step 1: Show the calculation (e.g., "5 x 50 = 250")
        const calculationText = `${Number(reward).toFixed(2)} x ${Number(multiplier).toFixed(0)}`;
        this.tumbleValue.text = calculationText;
        this.tumbleValue.alpha = 1;
        this.tumbleValue.anchor.set(0.5, 0.5);

        // Step 2: Animate the calculation text with a fade-in effect
        await new Promise<void>((resolve) => {
            Tweener.addTween(this.tumbleValue, {
                alpha: 1,
                time: 0.5,
                transition: "easeInOut",
                onComplete: () => {
                    resolve();
                }
            });
        });

        // Step 3: Pause briefly to show the calculation
        await this.delay(1000); // 1 second delay

        // Step 4: Animate counting up from 00 to the result value in the same position
        const obj = { val: 0 };
        await new Promise<void>((resolve) => {
            Tweener.addTween(obj, {
                val: newTotalReward,
                time: duration,
                onUpdate: () => {
                    this.tumbleValue.text = `$${obj.val.toFixed(2)}`; // Update text with counting value
                },
                onComplete: () => {
                    resolve();
                },
                transition: "easeInOut",
            });
        });


