import { Container, Text } from 'pixi.js';
import LayoutBuilder from '../../gamma-engine/core/utils/LayoutBuilder';
import ValueText from '../../gamma-engine/core/view/text/ValueText';
import { Tweener } from '../../libs/engineTween';
import SoundManager from '../../gamma-engine/core/sound/SoundManager';
import SoundList from '../../gamma-engine/common/sound/SoundList';
import Wallet from '../../gamma-engine/slots/model/Wallet';
import SoundListExtended from '../sound/SoundListExtended';
import Sound from '../../gamma-engine/core/sound/Sound';

export class ReelHeader extends Container {
    protected tfCurrentWinValue: ValueText;
    protected tfText: Text;
    private loopedSound: Sound;
    private isMobile: boolean = this.checkIsMobile();

    constructor(le) {
        super();
        LayoutBuilder.create(le, this, (le) => {
            return this.customClassElementCreate(le);
        });

        this.tfCurrentWinValue.renderValueFunction = (tfText, value) => {
            tfText.text = Wallet.getCurrencyValueFixed(value, 2, true);
        };
        this.tfCurrentWinValue.setValue(0);
        this.tfCurrentWinValue.visible = false;

        // Apply initial scale
        this.updateScale();

        // Listen for device changes
        window.addEventListener('resize', this.onResize.bind(this));
    }

    private checkIsMobile(): boolean {
        return window.innerWidth < 768; // Adjust this value as needed
    }

    private onResize(): void {
        const newIsMobile = this.checkIsMobile();
        if (newIsMobile !== this.isMobile) {
            this.isMobile = newIsMobile;
            this.updateScale();
        }
    }

    private updateScale(): void {
        if (this.isMobile) {
            this.scale.set(1.2, 1.3);
        } else {
            this.scale.set(1, 1);
        }
    }

    customClassElementCreate(le) {
        let instance = null;
        switch (le.customClass) {
            case 'ValueText':
                instance = new ValueText(le);
                break;
        }
        return instance;
    }

    setHeader(value, shake = false) {
        this.tfText.visible = value <= 0;
        this.tfCurrentWinValue.visible = !this.tfText.visible;
        if (!value) return;
        if (value <= 0) return;

        // Ensure previous tween finishes before starting a new one
        if (Tweener.isTweening(this.tfCurrentWinValue)) {
            Tweener.removeTweens(this.tfCurrentWinValue);
            this.tfCurrentWinValue.value = value;
            return;
        }

        if (value < this.tfCurrentWinValue.value) {
            this.tfCurrentWinValue.value = 0;
        }

        this.tfCurrentWinValue.setValue(value, {
            countUpDuration: 1.5,
        });

        if (!shake) return;

        this.loopedSound = SoundManager.loop({
            id: SoundList.COUNTER_LOOP,
            volume: 0.35,
        });

        Tweener.addTween(this.tfCurrentWinValue.scale, {
            x: 1,
            y: 1,
            transition: 'easeOutSine',
            time: 0.35,
            onComplete: () => {
                Tweener.addTween(this.tfCurrentWinValue.scale, {
                    x: 1,
                    y: 1,
                    transition: 'easeOutElastic',
                    time: 0.3,
                });

                this.loopedSound.stop();
            },
        });
    }
}
