import {Container, Graphics, Text} from "pixi.js";
import LayoutBuilder from "../../../core/utils/LayoutBuilder";
import AssetsManager from "../../../core/assets/AssetsManager";
import SymbolView from "../../../slots/view/SymbolView";
import {SymbolsList} from "../../../../dragon-queen/view/SymbolsList";
import TextUtils from "../../../core/utils/TextUtils";
import {autoscaleText} from "../../../core/utils/Utils";
import Wallet from '../../../slots/model/Wallet';
import { container } from 'tsyringe';

export class CascadeHistoryCell extends Container{
    private background: Graphics;
    private tfCount: Text;
    private tfWin: Text;
    private wallet:Wallet;
    private symbolSlot: Container;
    private config:{
                symbolId: number,
                symbolCount: number,
                payout: number
            }

    constructor(config:{
        symbolId: number,
        symbolCount: number,
        payout: number
    }) {
        super();
        this.config=config;
        LayoutBuilder.create(AssetsManager.layouts.get('CascadeHistoryCell-cascadeHistoryCell'), this);
        this.wallet= container.resolve(Wallet);
        this.tfCount.text = config.symbolCount;
        this.tfWin.text =   this.wallet.getCurrencyValue(config.payout,true);
        this.createSymbolInSlot(config.symbolId);

        autoscaleText(this.tfWin, 20, this.tfWin.width, this.tfWin.height);
    }

    private createSymbolInSlot(symbolId: number): void{
        this.symbolSlot['area'].visible = false;
        const symbol: SymbolView = new SymbolView(SymbolsList.find((symbol)=>symbol.id == symbolId));
        symbol.width = this.symbolSlot['area'].width;
        symbol.scale.y = symbol.scale.x = 0.25;
        //symbol.y=14;
        this.symbolSlot.addChild(symbol);
        symbol.y = 6
    }
    public updateWin(multiplier:number){
                this.tfWin.text = this.wallet.getCurrencyValue(this.config.payout*multiplier,true);
            }
        
            public get WinText():Text{
                return this.tfWin;
        }

}