import { Spine } from '@pixi-spine/all-4.1';
import AssetsManager from '../../gamma-engine/core/assets/AssetsManager';
import { Container, Sprite } from 'pixi.js';
import Sound from '../../gamma-engine/core/sound/Sound';
import SoundManager from '../../gamma-engine/core/sound/SoundManager';
import SoundListExtended from '../sound/SoundListExtended';
import IAdjustableLayout from '../../gamma-engine/core/view/IAdjustableLayout';
import { UpdateLayoutDescription } from '../../gamma-engine/core/view/UpdateLayoutDescription';
import SoundList from '../../gamma-engine/common/sound/SoundList';
import { ScreenOrientation } from '../../gamma-engine/core/view/ScreenOrientation';
import { Tweener } from '../../libs/engineTween';

export default class MainScreenBackground extends Container implements IAdjustableLayout {
    private static DEFAULT_BACKGROUND_CHANNEL: string = 'ambient';
    public static instance: MainScreenBackground;
    private _theme: BackgroundType;
    private backgroundMusic: Sound;

    private currentAnimation: Spine;

    // VISUALS
    private mainAnimationDesktop: Spine;
    private mainAnimationMobile: Spine;

    private isMobile: number = -1;

    constructor() {
        super();

        // Load both mobile & desktop Spine animations
        this.mainAnimationDesktop = new Spine(AssetsManager.spine.get(BackgroundType.NORMAL));
        this.mainAnimationMobile = new Spine(AssetsManager.spine.get(BackgroundType.NORMAL_MOBILE));

        this.theme = BackgroundType.NORMAL; // Set initial theme
        this.updateBackgroundMusic();

        // Listen for screen resize to dynamically update
        MainScreenBackground.instance = this;
    }

    /**
     * Dynamically switches the Spine animation based on mobile/desktop
     */
    private forceAnimationUpdate(): void {
        const newAnimation = this.isMobile == 1 ? this.mainAnimationMobile : this.mainAnimationDesktop;

        if (this.currentAnimation !== newAnimation) {
            this.swap(newAnimation, 'base_game', true);
        }
    }

    /**
     * Sets the theme and updates the animation accordingly
     */
    public set theme(type: BackgroundType) {
        if (this._theme === type) return;

        this._theme = type;
        switch (this._theme) {
            case BackgroundType.NORMAL:
                this.swap(this.isMobile ? this.mainAnimationMobile : this.mainAnimationDesktop, 'base_game', true);
                break;
            case BackgroundType.FREEGAME:
                this.swap(this.isMobile ? this.mainAnimationMobile : this.mainAnimationDesktop, 'free_game', true);
                break;
        }
    }

    public get theme() {
        return this._theme;
    }

    /**
     * Swaps the Spine animation while preserving smooth transitions
     */
    private swap(animation: Spine, id: string, loop: boolean): void {
        if (this.currentAnimation) {
            this.removeChild(this.currentAnimation); // Remove previous animation
        }

        this.currentAnimation = animation;
        this.addChild(this.currentAnimation);

        // Smooth transition for background music if needed
        if (this.backgroundMusic) {
            Tweener.addTween(this.backgroundMusic, {
                time: 0.45,
                transition: 'linear',
                onComplete: () => {
                    this.updateBackgroundMusic();
                },
            });
        }

        this.currentAnimation.state.setAnimation(0, id, loop);
    }

    /**
     * Updates background music based on theme
     */
    private updateBackgroundMusic(): void {
        if (this.backgroundMusic) {
            this.backgroundMusic.stop();
        }

        this.backgroundMusic = SoundManager.loop({
            id: this._theme === BackgroundType.NORMAL ? SoundListExtended.BASEGAME_BACKGROUND : SoundListExtended.FREEGAME_BACKGROUND,
            volume: this._theme === BackgroundType.NORMAL ? 0.3 : 0.5,
            channel: MainScreenBackground.DEFAULT_BACKGROUND_CHANNEL,
        });
    }

    /**
     * Adjusts layout for different screen orientations
     */
    public updateLayout(desc: UpdateLayoutDescription) {
        let newIsMobile: number = -1;
        switch (desc.orientation) {
            case ScreenOrientation.HORIZONTAL:
                newIsMobile = 0;
                break;
            case ScreenOrientation.VERTICAL:
                newIsMobile = 1;
                break;
        }
        const xScale: number = desc.currentWidth / desc.baseWidth;
        const yScale: number = desc.currentHeight / desc.baseHeight;
        this.scale.set(xScale > yScale ? xScale : yScale);
        if(newIsMobile !== this.isMobile) {
            this.isMobile = newIsMobile;
            this.forceAnimationUpdate();
        }
    }
}

/**
 * Enum for Background Types
 */
export enum BackgroundType {
    NORMAL = 'main-screen-background',
    NORMAL_MOBILE = 'main-screen-background-mobile',
    FREEGAME = 'freegame-screen-background',
    WINNING = 'winning',
}
