import { Container, Sprite, Text } from 'pixi.js';
import LayoutBuilder from '../../../core/utils/LayoutBuilder';
import AssetsManager from '../../../core/assets/AssetsManager';
import { errorMessageCode } from '../../../../dragon-queen/services/text-json-files/DataSource';
import LayoutElement from '../../../core/view/model/LayoutElement';
import Button from '../../../core/view/ui/Button';


export default class PopupConnectionLost extends Container {
    private tfMessage: Text;
    protected icon: Button;
    constructor(errorMessage: string,le:LayoutElement) {
        super();
        LayoutBuilder.create(le, this, (le: LayoutElement) => {
            return this.customClassElementCreate(le);
        });
        this.interactive = true;
        this.on('pointerdown', this.onIconClick,this);
        this.tfMessage.text = errorMessageCode[errorMessageCode.hasOwnProperty(errorMessage)? errorMessage: 'UNFINISHED_SLOT_GAME_BET_EXISTS']
    }

    protected onIconClick(): void {
        console.log('reload');
        location.reload();
    }
    public customClassElementCreate(le: LayoutElement): unknown {
        let instance: unknown = null;
        switch (le.customClass) {
            case 'Button':
                instance = new Button(le);
                break;
        }
        return instance;
    }
}
