import {Spine } from '@pixi-spine/all-4.1';
import AssetsManager from '../../gamma-engine/core/assets/AssetsManager';
import SoundList from '../../gamma-engine/common/sound/SoundList';
import SoundManager from '../../gamma-engine/core/sound/SoundManager';

export default class Character extends Spine {
    private isAnim: boolean = false;
    private currentAnimation: string = Character.AnimationIdle;

    public static readonly AnimationIdle: string = 'idle'; // Store current animation for smooth transition
    public static readonly AnimationIdle2: string = 'idle02'; // Store current animation for smooth transition
    public static readonly AnimationWin1: string = 'match'; // Store current animation for smooth transition
    public static readonly AnimationWin2: string = 'match'; // Store current animation for smooth transition
    public static readonly AnimationScatter: string = 'Scatter'; // Store current animation for smooth transition
    public static readonly AnimationMultiply: string = 'multiplier'; // Store current animation for smooth transition

    constructor() {
        super(AssetsManager.spine.get('character'));

        const mixTime = 0.25;

        this.state.data.setMix(Character.AnimationIdle, Character.AnimationIdle2, mixTime);
        this.state.data.setMix(Character.AnimationIdle, Character.AnimationWin1, mixTime);
        this.state.data.setMix(Character.AnimationIdle, Character.AnimationWin2, mixTime);
        this.state.data.setMix(Character.AnimationIdle, Character.AnimationScatter, mixTime);
        this.state.data.setMix(Character.AnimationIdle, Character.AnimationMultiply, mixTime);
        this.state.data.setMix(Character.AnimationIdle2, Character.AnimationIdle, mixTime);
        this.state.data.setMix(Character.AnimationIdle2, Character.AnimationWin1, mixTime);
        this.state.data.setMix(Character.AnimationIdle2, Character.AnimationWin2, mixTime);
        this.state.data.setMix(Character.AnimationIdle2, Character.AnimationScatter, mixTime);
        this.state.data.setMix(Character.AnimationIdle2, Character.AnimationMultiply, mixTime);
        this.state.data.setMix(Character.AnimationWin1, Character.AnimationIdle, mixTime);
        this.state.data.setMix(Character.AnimationWin1, Character.AnimationIdle2, mixTime);
        this.state.data.setMix(Character.AnimationWin1, Character.AnimationWin2, mixTime);
        this.state.data.setMix(Character.AnimationWin1, Character.AnimationScatter, mixTime);

        this.state.setAnimation(0, this.currentAnimation, true);
    }

    /**
     * Plays a win animation and switches back to idle when done
     */
    public playWinAnimation(animationName: string, isAnim: boolean): void {
        if (isAnim && this.isAnim) {
            return;
        }

        if (isAnim || (!this.isAnim && this.currentAnimation != animationName)) {
            this.isAnim = isAnim;
            const newAnimationName = animationName;
            this.state.setAnimation(0, newAnimationName, false);
            if(Math.random() < 0.5) {
                SoundManager.play(SoundList.HERO_ANIMATION);
            }
            setTimeout(() => {
                this.isAnim = false;
                this.state.setAnimation(0, this.currentAnimation, true);
            }, 2500);
        }

        if (!isAnim) {
            this.isAnim = false;
            this.currentAnimation = animationName;
        }
    }
}
