import {Spine } from '@pixi-spine/all-4.1';
import AssetsManager from '../../core/assets/AssetsManager';
import { BrowserApplication } from '../../core/view/BrowserApplication';
import { ScreenOrientation } from '../../core/view/ScreenOrientation';

export default class Logo extends Spine {
    private isAnim:boolean = false;
    constructor() {
        super(AssetsManager.spine.get('logo'));
        this.visible=false;
        this.state.data.setMix('active','idle', 0.15);
        this.state.data.setMix('idle','active',0.15);
        this.state.setAnimation(0, 'idle', true);

        window.addEventListener('resize', this.onResize.bind(this));
        this.onResize();
    }

    private onResize():void{
        const isMobile = BrowserApplication.mainScreenStage.orientation === ScreenOrientation.VERTICAL;
        this.alpha = isMobile ? 1 : 0;
    }

    public playWinAnimation():void{
        if(this.isAnim) return;
        
        this.isAnim = true;
        this.state.setAnimation(0,'active',false);
        setTimeout(() => {
            this.state.setAnimation(0,'idle',true);
            this.isAnim = false;
        }, 3000);
    }
}

