
import { BitmapFont, BitmapText, Circle, Ellipse, Polygon, Rectangle, RoundedRectangle, Sprite, Text } from 'pixi.js';
import ButtonAutoSpinStopAnimation from './ButtonAutoSpinStopAnimation';
import Button from '../../core/view/ui/Button';
import LayoutElement from '../../core/view/model/LayoutElement';
import AssetsManager from '../../core/assets/AssetsManager';




export default class ButtonAutoSpinStop extends Button {
    // VISUALS

    private buttonAnimator: ButtonAutoSpinStopAnimation;

    private currentAnim: string;

    constructor(le: LayoutElement, customClassResolver: (le: LayoutElement) => any = null, hitArea: Rectangle | Circle | Ellipse | Polygon | RoundedRectangle = null) {
        super(le, customClassResolver, hitArea);

        this.buttonAnimator = (this.normal as ButtonAutoSpinStopAnimation);
    }

    // PRIVATE API
    protected customClassElementCreate(le: LayoutElement): unknown {
        let instance: unknown = null;

        switch (le.customClass) {
            case 'ButtonAutoSpinStopAnimation':
                instance = new ButtonAutoSpinStopAnimation(
                    new BitmapText('00', {
                        fontName: 'Alexandria',
                        fontSize: 57,
                        align: 'center',
                    })
                );
                break;
            default:
                instance = super.customClassElementCreate(le);
                break;
        }

        return instance;
    }
    public setAnimation(name: string) {
        this.buttonAnimator.setAnimation(name);
    }
    public setAutoPlayLeftValue(value) {
        this.buttonAnimator.setAutoPlayLeftValue(value);
    }
}
