import {Container, DisplayObject, Graphics, Sprite, Text} from 'pixi.js';
import LayoutBuilder from '../../../core/utils/LayoutBuilder';
import AssetsManager from '../../../core/assets/AssetsManager';
import { SymbolData } from '../../../slots/view/SymbolData';
import { container } from 'tsyringe';
import SlotMachine from '../../../slots/model/SlotMachine';
import Wallet from '../../../slots/model/Wallet';
import {SlotMachineEvent} from '../../../slots/model/event/SlotMachineEvent';
import {WalletEvent} from '../../../slots/model/event/WalletEvent';
import {autoscaleText} from '../../../core/utils/Utils';
import LayoutElement from '../../../core/view/model/LayoutElement';
import GameService from '../../../../dragon-queen/services/GameService';

type PaytableSymbolCellConfig = {
    setMultipliers: boolean,
    maxFontSize: number,
    maxWidth: number,
    maxHeight: number
}

export default class PaytableSymbolCell extends Container {
    private item_2


    // private tfPayscompartment12_30:Text
    // private tfPayscompartment10_11:Text
    // private tfPayscompartment8_9:Text

    private tfPays6:Text;
    private tfPays4:Text;
    private tfPays5:Text;
    private symbolId: number;

    private symbolArea: Graphics;
    private staticIcon: Container;

    private multiplierDescription: Container;
    private slotMachine:SlotMachine
    private wallet:Wallet

    private defaultMaxSize: number;
    private parentContainer: Container;

    private config: PaytableSymbolCellConfig = {
        setMultipliers: true,
        maxFontSize: 20,
        maxHeight: 60,
        maxWidth: 120
    };
    constructor(le: LayoutElement, symbolData: SymbolData, config: PaytableSymbolCellConfig = null, parentContainer:Container = null) {
        super();

        LayoutBuilder.create(le, this);
        this.parentContainer = parentContainer ? parentContainer : this;
        if(config)
            this.config = config;

        this.symbolId = symbolData.id;
        this.wallet = container.resolve(Wallet)
        this.slotMachine = container.resolve(SlotMachine)
        this.slotMachine.on(SlotMachineEvent.BET_VALUE_CHANGED, () =>{this.setMultipliers(this.symbolId)}, this);

        this.wallet.on(WalletEvent.COIN_VALUE_CHANGED,() =>{this.setMultipliers(this.symbolId)}, this);

        this.symbolArea.visible = false;

        const staticIcon: Sprite = new Sprite(symbolData.staticIcon.texture);
        staticIcon.anchor.set(0.5, 0.5);
        this.staticIcon.addChild(staticIcon);

        this.tfPays6 = this.parentContainer['multiplierDescription']['tfPays6'];
        this.tfPays5 = this.parentContainer['multiplierDescription']['tfPays5'];
        this.tfPays4 = this.parentContainer['multiplierDescription']['tfPays4'];

        this.setMultipliers(symbolData.id);
        this.multiplierDescription.visible = this.config.setMultipliers;
    }

    private updateView(maxWidth: number = null, texts: Text[] = null): void{
        // const texts: Text[] =  this['multiplierDescription'].children as Text[];

        [this.tfPays4, this.tfPays5, this.tfPays6].forEach((text: Text)=>{
            //autoscaleText(text,this.config.maxFontSize, this.config.maxWidth, this.config.maxHeight);
        });

        const offset: number = 12;

        const totalWidth: number = maxWidth?maxWidth: this.width;

        for (let i = 0; i < 3; i++) {
            texts[i].x = (totalWidth- (texts[i].width+offset+texts[i+3].width)) /2;
            const posX = texts[i].x + texts[i].width + offset;
            texts[i+3].x = posX + texts[i+3].width;
        }
    }

    public get width(): number {
        return this.symbolArea.width;
    }

    public get height(): number {
        return this.symbolArea.height;
    }

    private setMultipliers(symbolId: number): void {
        const sm: SlotMachine = container.resolve(SlotMachine);
        const isDoubleChance = container.resolve(GameService).doubleUpChance;
        const totalBet = isDoubleChance ? container.resolve(SlotMachine).totalBet * 0.8 : container.resolve(SlotMachine).totalBet;
        
        if (symbolId > 100 && symbolId < 8000) {
            this.tfPays6.text = `${this.wallet.getCurrencyValue(sm.findRule(symbolId, 12).reward.line.multiplier * totalBet)}`;
            this.tfPays5.text = `${this.wallet.getCurrencyValue(sm.findRule(symbolId, 10).reward.line.multiplier * totalBet)}`;
            this.tfPays4.text = `${this.wallet.getCurrencyValue(sm.findRule(symbolId, 8).reward.line.multiplier * totalBet)}`;
        }else if(symbolId == 25){
            this.tfPays6.text = `${this.wallet.getCurrencyValue(sm.findRule(symbolId, 6).reward.line.multiplier * totalBet)}`;
            this.tfPays5.text = `${this.wallet.getCurrencyValue(sm.findRule(symbolId, 5).reward.line.multiplier * totalBet)}`;
            this.tfPays4.text = `${this.wallet.getCurrencyValue(sm.findRule(symbolId, 4).reward.line.multiplier * totalBet)}`;
        }
        this.updateView(this.width, this.parentContainer['multiplierDescription'].children as Text[]);
    }
}
