import { BitmapText, Container, Sprite, Text } from 'pixi.js';
import LayoutBuilder from '../../../core/utils/LayoutBuilder';
import AssetsManager from '../../../core/assets/AssetsManager';
import { Spine } from '@pixi-spine/all-4.1';
import LayoutElement from '../../../core/view/model/LayoutElement';
import Sound from '../../../core/sound/Sound';
import { container } from 'tsyringe';
import Wallet from '../../../slots/model/Wallet';
import { autoscaleText } from '../../../core/utils/Utils';
import { Translation } from '../../../core/utils/Translation';

export default class PopupFreeSpinWinning extends Container {
    // VISUALS
    public animation: Spine;
    // public coinAnimation: Spine;
    public tfAmount: Text | BitmapText;

    constructor(private amount: number | (() => number),scale?:number) {
        super();

        // DisplayShortcuts.init();

        LayoutBuilder.create(AssetsManager.layouts.get('PopupFreeSpinWinning'), this, (le: LayoutElement) => {
            return this.customClassElementCreate(le);
        });
        const wallet=container.resolve(Wallet);
        this.tfAmount.text = wallet.getCurrencyValue(amount instanceof Function ? amount() : amount)
        this.tfAmount.anchor.set(0.5, 1);
        this.tfAmount.scale.set(0.95,-0.95)
        this.tfAmount.y = 20

        this.animation = new Spine(AssetsManager.spine.get('free_spin_popups'))
        //   this.animation.scale.set(0.5)
        // this.coinAnimation = new Spine(AssetsManager.spine.get('winning_coins'))
        this.addChild(this.animation)
        for(let i = 0; i < this.animation.skeleton.slots.length; i++){
                const slot = this.animation.skeleton.slots[i];
                const slotContainer = this.animation.slotContainers[i];
                slotContainer.name = slot.data.name;
            }
        setTimeout(()=>{
            const counterContainer: Container = this.animation.slotContainers[this.animation.skeleton.findSlotIndex('place_holder')];
            counterContainer.removeChildren();
            counterContainer.addChild(this.tfAmount);

            this.updateSpineWithText('Press', Translation.t('tfpressanywhereto'), 0xFFFFFF, 20, 'Alexandria');
            this.updateSpineWithText('You Have', Translation.t('tfYouHaveWon'), 0xFFFFFF, 62, 'Alexandria-Bold');
            this.updateSpineWithText('Free Spin', Translation.t('tfIn15FreeSpins'), 0xFFFFFF, 62, 'Alexandria-Bold');
          },100)
        
        this.once('removed', this.onRemoved, this);
        this.once('added', this.onAdded, this);
        if(scale){
            this.animation.scale.set(scale);
        }
        
    }
    private updateSpineWithText(containerName: string, text: string, color: number = 0xFFFFFF, fontSize: number = 32, fontName: string = 'DalekPinpoint'): void {
        const container = this.animation.slotContainers[this.animation.skeleton.findSlotIndex(containerName)];
        container.removeChildren();
        const textBitmap = new BitmapText(text, {
            fontName: fontName,
            fontSize: fontSize,
            align: 'center',
            tint: color
        });
        textBitmap.anchor.set(0.5, 0.7);
        textBitmap.scale.y = -1;
        container.addChild(textBitmap);
    }
    
    // PRIVATE API
    private customClassElementCreate(le: LayoutElement): unknown {
        let instance: unknown = null;

        switch (le.customClass) {
            // case 'TransitionAnimation':
            //     instance = new Spine(AssetsManager.spine.get('transition_animation'));
            //     break;
            // case 'FreespinAnimation':
            //     instance = this.animation
                
            //     break;
        }

        return instance;
    }

    private onAdded(): void {
        const wallet=container.resolve(Wallet);
        this.tfAmount.text = wallet.getCurrencyValue(this.amount instanceof Function ? this.amount() : this.amount)
        //autoscaleText(this.tfAmount, 100, 821.541, 244.5);
        this.animation.state.setEmptyAnimations(0);
        this.animation.state.setAnimation(0, 'end_loop', true);
        setTimeout(()=>{
            const uslessContainer: Container = this.animation.slotContainers[this.animation.skeleton.findSlotIndex('FREE SPINS')];
            uslessContainer.removeChildren();
        },100)
        // this.coinAnimation.state.setEmptyAnimations(0)
        // this.coinAnimation.state.setAnimation(0,'animation', true)
    }

    private onRemoved(): void {
        this.animation.state.setEmptyAnimations(0);
        this.animation.state.clearListeners();
        this.animation['lastTime'] = null;
    }
}