import { Spine } from "@pixi-spine/all-4.1";

import { BitmapText, Container, Text } from "pixi.js";
import AssetsManager from "../../core/assets/AssetsManager";
import { BrowserApplication } from "../../core/view/BrowserApplication";
import { ScreenOrientation } from "../../core/view/ScreenOrientation";


export default class ButtonAutoSpinStopAnimation extends Spine {
    public static SPIN: string = 'auto_spin_click';
    public static LOOP: string = 'auto_spin_idle';
    public static INFINITE_SPIN: string = 'infinity_stop_click';
    public static INFINITE_LOOP: string = 'infinity_stop_idle';
    private text: BitmapText
    constructor(text: BitmapText) {
        super(AssetsManager.spine.get('button-spin'));
        this.text = text;
        this.text.x = 0;
        this.text.y = 0;
        this.text.pivot.x = this.text.width / 2;
        const counterContainer: Container = this.slotContainers[this.skeleton.findSlotIndex('22')];
        //counterContainer.removeChildren();
        this.updateSpineAnims();
        BrowserApplication.mainScreenStage.on("change-orientation", this.updateSpineAnims, this);
        counterContainer.parent.addChild(this.text);

    }

    private updateSpineAnims() {
        if(BrowserApplication.mainScreenStage.orientation===ScreenOrientation.VERTICAL){
            ButtonAutoSpinStopAnimation.SPIN="m_auto_spin_click";
            ButtonAutoSpinStopAnimation.LOOP="m_auto_spin_idle";
            ButtonAutoSpinStopAnimation.INFINITE_SPIN="m_infinity_stop_click";
            ButtonAutoSpinStopAnimation.INFINITE_LOOP="m_infinity_stop_idle";
            this.text.fontSize = 11;
        }else{
            ButtonAutoSpinStopAnimation.SPIN="auto_spin_click";
            ButtonAutoSpinStopAnimation.LOOP="auto_spin_idle";
            ButtonAutoSpinStopAnimation.INFINITE_LOOP="infinity_stop_click";
            ButtonAutoSpinStopAnimation.INFINITE_LOOP="infinity_stop_idle";
            this.text.fontSize = 55;
        }

        this.setAnimation(ButtonAutoSpinStopAnimation.LOOP);
    }
    public setAnimation(name: string): void {
        const infini = name === ButtonAutoSpinStopAnimation.INFINITE_LOOP;
        this.text.visible = !infini;
        this.state.setAnimation(0, name,
            name === ButtonAutoSpinStopAnimation.LOOP ||
            name === ButtonAutoSpinStopAnimation.INFINITE_LOOP)
    }
    public setAutoPlayLeftValue(value: number) {
        this.text.text = value.toString();
        this.text.pivot.x = this.text.width / 2;
    }
}