export type LoginResponse = {
    Player: {
        Balance: number,
        Rate: number,
        Currency: string,
        CoinRate:number
    },
    Current: {
        Type: number,
        TotalWin: number,
        AccWin: number,
        MaxWin:number,
        Multiplier: number,
        Result: {
            R: string,
            LR: number,
            WR?:{
                T: number,
                AWP: number[][],
                R: string
            }[],
        },
        Round: {
            RoundType: number,
            Bet: number,
            ActualBet: number,
            BetValue: number,
            Line: number,
            LineBet: number,
            Payout: number,
            Items: string[],
            AccMultiplier: number;
        },
        FreeSpin:{
            Current: number,
            Total: number
        }
    },
    Next?: {
        Type: number,
        isAnteBet:boolean,
        FreeSpin:{
            Next: number,
            Total: number
        }
        M: number
    },
    GameInfo: {
        Line: number,
        BetValue: number[],
        LineBet: number[],
        BuyFeature:{
            RoundType: number,
            Rate: number,
            Total: number
        }
    },
        Details?:{
            betId:number,
            gameId:number,
            userId:number
    }
};

