import { Spine } from '@pixi-spine/all-4.1';
import AssetsManager from '../../core/assets/AssetsManager';
import { container } from "tsyringe";
import SlotMachine from "../../slots/model/SlotMachine";
import { SlotMachineState } from "../../slots/model/SlotMachineState";
import { BrowserApplication } from '../../core/view/BrowserApplication';
import { ScreenOrientation } from '../../core/view/ScreenOrientation';


export default class ButtonSpinAnimation extends Spine {
    public static SPIN: string = 'spin_click';
    public static LOOP: string = 'stop_idle';
    public static STOP: string = 'stop_idle';
    public static WAIT: string = 'spin_idle';
    public static WAIT_DELAY: string = 'spin_idle';
    public static WAIT_DELAY_INTRO: string = 'spin_idle';

    constructor() {
        super(AssetsManager.spine.get('button-spin'));
        this.state.data.setMix(ButtonSpinAnimation.SPIN, ButtonSpinAnimation.LOOP, 0.15);
        this.state.data.setMix(ButtonSpinAnimation.LOOP, ButtonSpinAnimation.STOP, 0.15);
        this.state.data.setMix(ButtonSpinAnimation.LOOP, ButtonSpinAnimation.SPIN, 0.15);
        this.state.data.setMix(ButtonSpinAnimation.WAIT_DELAY, ButtonSpinAnimation.SPIN, 0.15);
        if (container.isRegistered(SlotMachine)) {
            const sm: SlotMachine = container.resolve(SlotMachine);
            if (sm.currentState == SlotMachineState.IDLE) {
                this.updateSpineAnims();
                BrowserApplication.mainScreenStage.on("change-orientation", this.updateSpineAnims, this)
            }
        }
        this.setAnimation(ButtonSpinAnimation.WAIT_DELAY);
    }
    private updateSpineAnims() {
        if (BrowserApplication.mainScreenStage.orientation === ScreenOrientation.VERTICAL) {
            ButtonSpinAnimation.SPIN = "m_spin_click"
            ButtonSpinAnimation.LOOP = "m_spin_hover"
            ButtonSpinAnimation.STOP = "m_stop_idle"
            ButtonSpinAnimation.WAIT = "m_spin_idle"
            ButtonSpinAnimation.WAIT_DELAY = "m_spin_idle"
            ButtonSpinAnimation.WAIT_DELAY_INTRO = "m_spin_idle"
        } else {
            ButtonSpinAnimation.SPIN = "spin_click"
            ButtonSpinAnimation.LOOP = "spin_hover"
            ButtonSpinAnimation.STOP = "stop_idle"
            ButtonSpinAnimation.WAIT = "spin_idle"
            ButtonSpinAnimation.WAIT_DELAY = "spin_idle"
            ButtonSpinAnimation.WAIT_DELAY_INTRO = "spin_idle"
        }
        this.setAnimation(ButtonSpinAnimation.WAIT_DELAY);
    }

    public setAnimation(name: string): void {
        this.state.setAnimation(0, name,
            name === ButtonSpinAnimation.LOOP ||
            name === ButtonSpinAnimation.WAIT ||
            name === ButtonSpinAnimation.WAIT_DELAY ||
            name === ButtonSpinAnimation.WAIT_DELAY_INTRO);
    }

    public addAnimation(name: string, delay: number): void {
        this.state.addAnimation(0, name, name === ButtonSpinAnimation.LOOP, delay);
    }

    public getAnimationDuration(name: string, isSpinning: boolean): number {
        if (name == ButtonSpinAnimation.LOOP && isSpinning)
            return 0;

        return this.spineData.animations.find((animation) => animation.name === name).duration;
    }
    public onRemoved() {
        BrowserApplication.mainScreenStage.off("change-orientation", this.updateSpineAnims, this)
    }
}