import Button from '../../../core/view/ui/Button';
import LayoutElement from '../../../core/view/model/LayoutElement';
import {BitmapFont, BitmapText, Sprite, Text} from 'pixi.js';
import GameService from '../../../../dragon-queen/services/GameService';
import {container} from 'tsyringe';
import Wallet from '../../../slots/model/Wallet';
import {autoscaleText} from '../../../core/utils/Utils';
import {PopupData, PopupType} from "../../model/PopupState";
import ControlEvent from "../../../core/control/event/ControlEvent";
import {UIPanelEvent} from "../../control/event/UIPanelEvent";
import SlotMachine from "../../../slots/model/SlotMachine";
import {Slot} from "@pixi-spine/runtime-4.1";
import {UIEvent} from "../../../slots/control/event/UIEvent";
import {UIEventExtension} from "../../../../dragon-queen/control/event/UIEventExtension";
import {SlotMachineEvent} from "../../../slots/model/event/SlotMachineEvent";
import {Translation} from "../../../core/utils/Translation";
import {WalletEvent} from "../../../slots/model/event/WalletEvent";
import {GameServiceEvent} from "../../../../dragon-queen/services/event/GameServiceEvent";
import SoundManager from '../../../core/sound/SoundManager';
import SoundList from '../../sound/SoundList';
import SoundListExtended from '../../../../dragon-queen/sound/SoundListExtended';

export class FreeSpinButton extends Button{
    private bcg: Sprite;
    private bcg2: Sprite;

    private tfTitle: Text|BitmapText;
    private tfValue: Text|BitmapText;

    private startWidth: number;

    constructor(le: LayoutElement) {
        super(le);
        this.bcg = this.normal['bcg'];
        this.bcg2 = this.normal['bcg2'];

        this.tfTitle = this.normal['tfTitle'];
        this.tfValue = this.normal['tfValue'];
        this.startWidth = this.tfValue.width;

        if(this.tfTitle instanceof Text){
            (this.tfTitle as Text).style.align = 'center';
        }

        const sm: SlotMachine = container.resolve(SlotMachine);
        const wallet: Wallet = container.resolve(Wallet);
        const gs: GameService = container.resolve(GameService)
        sm.on(SlotMachineEvent.BET_VALUE_CHANGED, ()=>{
            if(!gs.doubleUpChance) {
                this.setTexts(Translation.t('freeSpins.buy'), wallet.getCurrencyValue(gs.featureBuyConfig.Rate * sm.totalBet * wallet.coinValue, true));

            }
        }, this);

        wallet.on(WalletEvent.COIN_VALUE_CHANGED, ()=>{
            this.setTexts(Translation.t('freeSpins.buy'), wallet.getCurrencyValue(gs.featureBuyConfig.Rate * sm.totalBet * wallet.coinValue, true));
        }, this);

        gs.on(GameServiceEvent.DOUBLE_CHANCE_CHANGED, ()=>{
            this.enabled = !gs.doubleUpChance;
            this.alpha = this.enabled? 1: 0.4;
        }, this);

        this.on('pointerup', this.onClick, this);

        this.switchToMultipler(false);
    }

    private onClick():void{
        const data: PopupData = {
            type: PopupType.FEATURE_BUY,
            hideOnClick: false,
            duration: -1,
            callbacks: null
        }
        new ControlEvent(UIPanelEvent.SHOW_POPUP, data).dispatch();
        SoundManager.play(SoundListExtended.BUY_FREE_SPIN)
    }

    public setTexts(title: string, value: string,freeSpinStarted:boolean=false): void{
        this.tfTitle.text = title;
        this.tfValue.text = value;
        const gs: GameService = container.resolve(GameService);
        const sm: SlotMachine = container.resolve(SlotMachine);
        if(freeSpinStarted){
            this.alpha=1;
        }else{
            this.alpha= !gs.doubleUpChance? 1: 0.4;
            this.enabled=!gs.doubleUpChance? true: false;
        }
        autoscaleText(this.tfValue, 50, this.startWidth, 80);
    }

    switchToMultipler(isMultipler: boolean) {
        if(isMultipler){
            this.bcg.visible = false;
            this.bcg2.visible = true;
        }else{
            this.bcg.visible = true;
            this.bcg2.visible = false;
        }
    }
}