// IntroScreen.ts

import { Graphics, Sprite, Text, TextStyle } from 'pixi.js';
import ButtonSpin from '../../gamma-engine/common/view/ButtonSpin';
import { IntroScreenEvent } from '../../gamma-engine/common/view/event/IntroScreenEvent';
import SoundManager from '../../gamma-engine/core/sound/SoundManager';
import SoundListExtended from '../sound/SoundListExtended';
import LayoutBuilder from '../../gamma-engine/core/utils/LayoutBuilder';
import AssetsManager from '../../gamma-engine/core/assets/AssetsManager';
import LayoutElement from '../../gamma-engine/core/view/model/LayoutElement';
import AdjustableLayoutContainer from '../../gamma-engine/core/view/AdjustableLayoutContainer';
import IntroScreenBackground from './IntroScreenBackground';
import { UpdateLayoutDescription } from '../../gamma-engine/core/view/UpdateLayoutDescription';
import { Tweener } from '../../libs/engineTween';
import { GamePlayIntro } from './GamePlayIntro'; // Adjust the import path accordingly
import Logo from '../../gamma-engine/common/view/Logo';
import { ToggleButton } from '../../gamma-engine/core/view/ui/ToggleButton';
import Logger from '../../gamma-engine/core/utils/Logger';
import ButtonSpinAnimation from '../../gamma-engine/common/view/ButtonSpinAnimation';
import { getFromLocalStorage, saveToLocalStorage } from '../model/LocalStorageUtils';
import MainScreenBackground from './MainScreenBackground';
import Character from './Character';

export default class IntroScreen extends AdjustableLayoutContainer {
    // VISUALS
    private logo: Sprite;
    private btnGetStarted: ButtonSpin;
    private background: MainScreenBackground;
    private tfDonotShowAgain: Text;
    private gamePlayIntro: GamePlayIntro;
    private backgroundSound: any;
    private fade: Sprite;
    private xScatter: Text
    private xScatter2: Text
    private skipBtn: ToggleButton;
    
    private messages: string[] = [];
    private messagesEN: string[] = [
        "FIND 4X SCATTER FOR FEATURE GAME",
        "WIN UP TO 5000X BET",
        "RANDOM MULTIPLIERS UP TOO 500X",
        "SYMBOL PAY ANYWHERE ON THE SCREEN",
    ];
    private messagesTR: string[] = [
        "4X SCATTER N ZELLK OYUNUNU BUL",
        "5000X BAHS KAZAN",
        "500X KATLAYICI KAZAN",
        "EKRANIN HER YERNDE ZELLK KAZAN",
    ];
    private messagesPT: string[] = [
        "ENCONTRAR 4X SCATTER PARA O JOGO DA CARRETEIRA",
        "GANHE AT 5000X APOSTA",
        "MULTIPLICADORES ALEATRIOS AT 500X",
        "SMBOLOS PAGAM EM QUALQUER LUGAR NA TELA",
    ];
    private currentMessageIndex: number = 0;
    private textInterval: NodeJS.Timeout;
    boundOnGetStartedClicked: any;
    constructor(language: string) {
        super(AssetsManager.layouts.get('intro-screen'));
        if (language == 'TR') {
            this.messages = this.messagesTR;
        } else if (language == 'PT') {
            this.messages = this.messagesPT;
        } else {
            this.messages = this.messagesEN;
        }
        LayoutBuilder.create(this.layout, this, (le: LayoutElement) => {
            return this.customClassElementCreate(le);
        });

        // Instantiate GamePlayIntro

        // Add event listeners
        this.once('added', this.onAdded, this);
        this.once('removed', this.onRemoved, this);

    }
    private startTextInterval(): void {
        const updateText = () => {
            if (this.xScatter) {
                // Update to the next message
                this.currentMessageIndex = (this.currentMessageIndex + 1) % this.messages.length;
                this.xScatter.text = this.messages[this.currentMessageIndex];
                // this.xScatter.x = desc.baseWidth / 2 - this.xScatter.width / 2;
            }
            if (this.xScatter2) {
                this.xScatter2.text = this.messages[this.currentMessageIndex];
            }
            console.log('startTextInterval');
        };

        // Change text every 4 seconds
        this.textInterval = setInterval(updateText, 4000);
        // Update immediately for the first time
        updateText();
    }

    private onSkipChanged(event: any): void {
        console.log('onSkipChanged', event);
        const settings = getFromLocalStorage('settings');
        settings.skipScreen = this.skipBtn.getIsStateOn();
        saveToLocalStorage('settings', settings);
    }

    public updateLayout(desc: UpdateLayoutDescription) {
        super.updateLayout(desc);
        if (this.fade) {
            this.fade.width = desc.currentWidth;
            this.fade.height = desc.currentHeight;
        }
    }

    private onAdded(): void {
        this.boundOnGetStartedClicked = this.onGetStartedClicked.bind(this);
        this.background.interactive = true;
        this.background.eventMode = 'static';
        
        this.background.on('pointerup', this.boundOnGetStartedClicked, this);
        this.background.cursor = 'default';
        // Handle button click
        if (this.btnGetStarted) {
            this.btnGetStarted.on('pointerup', this.boundOnGetStartedClicked, this);
        }
        // Set up text animations
        setTimeout(() => {
            if (this.tfDonotShowAgain) {
                Tweener.addTween(this.tfDonotShowAgain, {
                    alpha: 1,
                    transition: 'easeInSine',
                    time: 0.5,
                });
            }
        }, 4000);

        this.skipBtn.on(ToggleButton.STATE_CHANGED, this.onSkipChanged, this);
        const isSkipScreen = getFromLocalStorage('settings')?.skipScreen ?? false;
        this.skipBtn.setStateView(isSkipScreen);

        if (this.btnGetStarted) {
            this.btnGetStarted.buttonAnimator.setAnimation(ButtonSpinAnimation.WAIT);
        }
        this.backgroundSound = SoundManager.loop({
            id: SoundListExtended.INTRO_BACKGROUND,
            volume: 0.3,
            channel: 'ambient',
        });
        if (this.gamePlayIntro) {
            this.gamePlayIntro.updateTransform();
            const bounds = this.gamePlayIntro.getBounds();
        }

        this.gamePlayIntro.initAndStart();

        this.startTextInterval();
    }

    private onRemoved(): void {
        SoundManager.mute = false;
        Tweener.removeTweens(this);
        if (this.btnGetStarted) {
            this.btnGetStarted.buttonAnimator.state.setEmptyAnimations(0);
            this.btnGetStarted.buttonAnimator.visible = false;
            this.btnGetStarted.removeAllListeners();
            this.btnGetStarted = null;
        }
        if(this.skipBtn) {
            this.skipBtn.removeAllListeners();
            this.skipBtn = null;
        }
        this.removeAllListeners();
        this.gamePlayIntro.stopIntroSimulation();
        this.gamePlayIntro = null;
        // Remove GamePlayIntro if needed
        if (this.gamePlayIntro && this.gamePlayIntro.parent) {
            this.gamePlayIntro.parent.removeChild(this.gamePlayIntro);
        }
        if(this.textInterval) {
            clearInterval(this.textInterval);
            this.textInterval = null;
        }
        this.background.removeAllListeners();
        this.background = null;
        this.tfDonotShowAgain.removeAllListeners();
        this.tfDonotShowAgain = null;
        this.destroy({
            children: true,
        });
    }

    private customClassElementCreate(le: LayoutElement): unknown {
        let instance: unknown = null;
        //console.log('le.customClass', le.customClass);

        switch (le.customClass) {
            case 'ButtonGetStarted':
                instance = new ButtonSpin(le);
                break;
            case 'IntroScreenBackground':
                instance = new MainScreenBackground();
                break;
            case 'Logo':
                instance = new Logo();
                break;
            case 'ToggleButton':
               instance = new ToggleButton(le);
                 break;
            case 'GamePlayIntro':
                instance = new GamePlayIntro();
                break;
            case 'Character':
                instance = new Character();
                break
            // Add other custom classes if needed
        }

        return instance;
    }

    private onGetStartedClicked(): void {
        Logger.debug('on Get Started Clicked');
        if (this.backgroundSound) {
            this.backgroundSound.stop();
        }
        if (this.btnGetStarted) {
            this.btnGetStarted.enabled = false;
        }
        SoundManager.play(SoundListExtended.INTRO_SPIN_BTN);
        this.emit(IntroScreenEvent.ON_GET_STARTED_CLICKED);
        this.background.interactive = false;
        this.background.eventMode = 'none';
        this.background.removeAllListeners();
    }
}
