import LoadingProgressBar from './LoadingProgressBar';
import { Spine } from '@pixi-spine/all-4.1';
import AssetsManager from '../../core/assets/AssetsManager';
import { BitmapText, Sprite, Texture } from 'pixi.js';
import AdjustableLayoutContainer from '../../core/view/AdjustableLayoutContainer';
import { UpdateLayoutDescription } from '../../core/view/UpdateLayoutDescription';
import SoundManager from '../../core/sound/SoundManager';
import SoundList from '../sound/SoundList';
import { Translation } from '../../core/utils/Translation';
import { Tweener } from '../../../libs/engineTween';

export class LoadingScreen extends AdjustableLayoutContainer {

    private background: Spine;
    private character: Sprite;
    private characterLogo: Sprite;
    private progressBar: LoadingProgressBar;
    private footerText: Sprite;
    private gradientBottom: Sprite;
    private startButton: Sprite;
    private callback: () => void;
    private static finished: boolean = false;
    private isClickable: boolean = false;
    private targetResizeY: number = 0;
    private loadingComplete: boolean = false;
    constructor(onCompleteCallback: () => void) {
        super(null);
        const isMobile = this.isMobile();
        this.background = new Spine(AssetsManager.spine.get(isMobile ? 'dragon-queen-mobile' : 'dragon-queen-desktop'));
        
        this.addChild(this.background);
        this.background.state.setAnimation(0, "loading_screen", true)

        this.character = new Sprite(AssetsManager.textures.get('loading-hero'));
        this.character.anchor.set(0.5, 0.5);
        this.addChild(this.character);


        this.characterLogo = new Sprite(AssetsManager.textures.get('loadingLogo'));
        this.characterLogo.scale.set(0.9);
        this.characterLogo.anchor.set(0.5, 0.5);
        this.characterLogo.position.y = this.isMobile() ? 265 : 103.5;
        this.character.addChild(this.characterLogo);

        this.createGradientBottom();

        this.footerText = new Sprite(AssetsManager.textures.get('footerText'));
        this.footerText.anchor.set(0.5, 0.5);
        this.addChild(this.footerText);

        this.startButton = new Sprite(AssetsManager.textures.get('startbutton'));
        this.startButton.anchor.set(0.5, 0.5);
        this.startButton.scale.set(0);
        this.startButton.position.y = -151;

        const bitmapText = new BitmapText(Translation.t('tfstart'), {
            fontName: 'DalekPinpoint',
            fontSize: 50,
            align: 'center',
            //tint: 0xFFD7F7
        })
        bitmapText.anchor.set(0.5, 0.32);
        this.startButton.addChild(bitmapText);

        this.footerText.addChild(this.startButton);

        this.progressBar = new LoadingProgressBar();
        this.addChild(this.progressBar);

        this.callback = onCompleteCallback;

        this.on('added', () => {
            this.handleWindowResize();
            this.interactive = true;
            this.on('pointerup', this.onPopupClick, this);
        });
    }

    private onPopupClick(): void {
        if(!this.isClickable) {
            Howler.ctx.resume();
            this.isClickable = true;
        }
        if (LoadingScreen.finished) {
            this.interactive = false;
            setTimeout(() => {
                if (this.callback) {
                    this.callback();
                    this.callback = null;
                }
                this.isClickable = false;
            }, 100);
            SoundManager.play(SoundList.UI_BUTTON_CLICK);
            this.removeAllListeners();
        }
    }

    public updateLayout(desc: UpdateLayoutDescription): void {
        this.handleWindowResize();
    }

    private createGradientBottom(): void {
        // Create a gradient texture in memory
        const gradientCanvas = document.createElement('canvas');
        gradientCanvas.width = 1;
        gradientCanvas.height = 400; // Base height for the gradient

        const ctx = gradientCanvas.getContext('2d');
        const gradient = ctx.createLinearGradient(0, 0, 0, 400);
        gradient.addColorStop(0, 'rgba(255, 255, 255, 0)'); // top transparent
        gradient.addColorStop(1, 'rgb(255, 255, 255)'); // bottom blue
        ctx.fillStyle = gradient;
        ctx.fillRect(0, 0, 2, 400);

        const gradientTexture = Texture.from(gradientCanvas);
        Texture.addToCache(gradientTexture, 'fade');
        // Create sprite with gradient texture (will be sized in updateLayout)
        this.gradientBottom = new Sprite(gradientTexture);
        this.gradientBottom.tint = "#3F0000";
        this.gradientBottom.anchor.set(0, 1); // Anchor at bottom-left for easy positioning
        this.addChild(this.gradientBottom);
    }

    // PUBLIC API
    public start() {
    }

    public stop() {
        if (!LoadingScreen.finished) {
            LoadingScreen.finished = true;
            if (this.isClickable) {
                if (this.callback) {
                    this.callback();
                    this.callback = null;
                    this.interactive = false;
                }
            } else {
                Tweener.addTween(this.progressBar, {
                    alpha: 0,
                    time: 0.1,
                    transition: 'linear',
                    onComplete: () => {
                        this.loadingComplete = true;
                        // Style the button
                        Tweener.addTween(this.startButton.scale, {
                            x: 1,
                            y: 1,
                            time: 0.6,
                            transition: 'easeOutBounce'
                        });
                        this.targetResizeY = this.character.position.y - 70;
                        Tweener.addTween(this.character.position, {
                            y: this.targetResizeY,
                            time: 0.6,
                            transition: 'easeOutBounce',
                        });
                    }
                });
            }
        }
    }

    private handleWindowResize() {
        if (!this.parent) {
            return;
        }
        const parentScale = this.parent ? this.parent.scale.x : 1;
        const parentOffset = this.parent ? this.parent.position : { x: 0, y: 0 };
        const inverseParentScale = 1 / parentScale;
        this.scale.set(inverseParentScale);
        this.position.set(-parentOffset.x * inverseParentScale, -parentOffset.y * inverseParentScale);

        const baseWidth = 1920;  // Base game width
        const baseHeight = 1080; // Base game height
        const width = window.innerWidth;
        const height = window.innerHeight;
        const yScale = height / baseHeight;
        const xScale = width / baseWidth;
        const scaleMin = Math.min(xScale*1.8, yScale);
        const scaleMax = Math.max(xScale, yScale);

        const scale = Math.min(xScale * 2, yScale);
        // Background: Scale on Y-axis to be full screen
        const shouldUseMobile = this.isMobile();
    const desiredKey = shouldUseMobile ? "dragon-queen-mobile" : "dragon-queen-desktop";

    if (!this.background || this.background.spineData !== AssetsManager.spine.get(desiredKey)) {
        // Si ya haba uno, lo removemos
        if (this.background) {
            this.removeChild(this.background);
            this.background.destroy({ children: true });
        }

        // Creamos uno nuevo con el Spine correcto
        this.background = new Spine(AssetsManager.spine.get(desiredKey));
        this.addChildAt(this.background, 0); // lo mandamos al fondo
        this.background.state.setAnimation(0, "loading_screen", true);
    }
        this.background.scale.set(this.isMobile() ? scaleMin : scaleMax);
        this.background.x = width / 2;
        this.background.y = height;

        // Character: Always keep centered
        this.character.x = width / 2;
        if (this.isMobile()) this.character.y = height / 2;
        else this.character.y =  height / 2; // Adjust for desktop view
        
        this.character.scale.set(scale * 1.1);
        this.characterLogo.position.y = this.isMobile() ? 265 : 103.5;
        if(this.loadingComplete)this.characterLogo.position.y -= 70;
        // Gradient bottom: Always at bottom of screen, full width
        this.gradientBottom.width = width;
        this.gradientBottom.height = 400 * yScale; // Scale gradient height
        this.gradientBottom.x = 0;
        this.gradientBottom.y = height; // Bottom of screen since anchor is (0,1)

        // Footer text: Center horizontally at bottom, positioned within gradient area
        this.footerText.x = width / 2;
        this.footerText.y = height - (this.gradientBottom.height * 0.3); // Position in lower part of gradient
        this.footerText.scale.set(scale);

        // Progress bar: Center horizontally and position at bottom
        if (this.progressBar) {
            this.progressBar.x = width / 2;
            this.progressBar.y = height - (this.gradientBottom.height * 0.6); // Position above footer text
            this.progressBar.scale.set(scale);
        }
    }

    private isMobile(): boolean {
        return this.getAspectRatio() < 1;
    }

    private getAspectRatio(): number {
        const width = window.innerWidth;
        const height = window.innerHeight;
        const aspectRatio = +(width / height).toFixed(2);
        return aspectRatio;
    }

    public setProgress(progress: number) {
        if (this.progressBar) {
            this.progressBar.setProgress(progress);
        }
    }

    public simulateLoading() {
        // Removed simulated loading - now using real asset loading progress
        // Progress is updated via setProgress() calls from SugarDaddy.ts
    }

    public destroy() {
        this.background.destroy();
        this.character.destroy();
        this.characterLogo.destroy();
        this.footerText.destroy();
        this.gradientBottom.destroy();
        this.progressBar.destroy();
        super.destroy();
    }
}

enum LoadingState {
    START = 'start',
    PROGRESS = 'progress',
    END = 'end',
}
