import { Spine } from '@pixi-spine/all-4.1';
import AssetsManager from '../../gamma-engine/core/assets/AssetsManager';
import IAdjustableLayout from '../../gamma-engine/core/view/IAdjustableLayout';
import { ScreenOrientation } from '../../gamma-engine/core/view/ScreenOrientation';
import {Container, Sprite} from "pixi.js";

export default class ReelsBackground extends Container {
        public static readonly animationIdle: string = 'Idle';
        public static readonly animationFreeSpin: string = 'FreeSpin';
        public static readonly animationMatch: string = 'Match';
    public animation: Spine;
        private isAnim: boolean = true;
    private currentAnimation: string = ReelsBackground.animationIdle;
    timeoutAnimation: NodeJS.Timeout;
    constructor() {
        super();
        this.animation = new Spine(AssetsManager.spine.get('reel-background-animation'))
        
        this.animation.position.set(964.5, 506.5)
        this.addChild(this.animation)

        this.once('added', this.onAdded, this);
        this.animation.state.data.setMix(ReelsBackground.animationIdle, ReelsBackground.animationMatch, 0.25);
        this.animation.state.data.setMix(ReelsBackground.animationIdle, ReelsBackground.animationFreeSpin, 0.25);
        this.animation.state.data.setMix(ReelsBackground.animationFreeSpin, ReelsBackground.animationIdle, 0.25);
        this.animation.state.data.setMix(ReelsBackground.animationFreeSpin, ReelsBackground.animationMatch, 0.25);
        this.animation.state.data.setMix(ReelsBackground.animationMatch, ReelsBackground.animationIdle, 0.25);
        this.animation.state.data.setMix(ReelsBackground.animationMatch, ReelsBackground.animationFreeSpin, 0.25);

        this.animation.state.setAnimation(0, ReelsBackground.animationIdle, true)

    }

    private onAdded() {
        this.animation.state.setEmptyAnimations(0);
        this.animation.state.setAnimation(0, ReelsBackground.animationIdle, true)
    }

        public playWinAnimation(animationName: string, isAnim: boolean = true): void {
            let addAnimation = false;
            if (isAnim && this.isAnim) {
                clearTimeout(this.timeoutAnimation);
                this.timeoutAnimation = null;
                addAnimation = true;
            }
    
            if (isAnim || (!this.isAnim && this.currentAnimation != animationName)) {
                this.isAnim = isAnim;
                const newAnimationName = animationName;
                if (addAnimation) {
                    this.animation.state.addAnimation(0, newAnimationName, false);
                } else {
                    this.animation.state.setAnimation(0, newAnimationName, false);
                }
    
                this.timeoutAnimation = setTimeout(() => {
                    this.isAnim = false;
                    this.animation.state.setAnimation(0, this.currentAnimation, true);
                    clearTimeout(this.timeoutAnimation);
                    this.timeoutAnimation = null;
                }, 3500);
            }
    
            if (!isAnim) {
                this.currentAnimation = animationName;
            }
        }

    
    public reset() {
        this.animation.state.setEmptyAnimations(0)
    }
}
